import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import HorizontalTabWrapper from "../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../Ui/Tab/Horizontal/HorizontalTabCard";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import style from "../../FinanceManager/FinanceManager.module.css";
import { Suspense, lazy, useCallback, useState } from "react";
import { assestsUtilityUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import FormCalendarInput from "../../../Ui/Input/FormCalendarInput";
import LmsAllLeaveTeaching from "./LmsAllLeaveTeaching";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { useLmsUpdateCarryRule } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import {
  slashDateFormat,
  ymdSlashDateFormat,
} from "../../../Utils/Functions/dateUtility";
const LmsAllLeaveNonTeaching = lazy(() => import("./LmsAllLeaveNonTeaching"));
const LmsAllLeave = ({ leaveTypeToggle, lcid, instituteId }) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [state, setState] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [lmsUpdateCarryRule] = useLmsUpdateCarryRule();
  const onNavigate = useCallback(() => {
    let url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/lms`
        : `/q/${params.username}/member/staff/lms`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  const onDateGrab = (val) => {
    let modi = ymdSlashDateFormat(val);
    setState(slashDateFormat(modi));
    if (lcid) {
      setDisabled((pre) => !pre);
      lmsUpdateCarryRule({
        lcid: lcid,
        flow: "Leave_Start",
        year: modi ? modi?.substring(0, 4) : "",
        date: modi ? modi?.substring(8, 10) : "",
        month: modi ? modi?.substring(5, 7) : "",
        int_month: modi ? +modi?.substring(8, 10) : "",
        carryRule: { calendar: modi },
      })
        .then(() => {
          // onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <div className={style.fm_header_start_container}>
            <img
              alt="search icon"
              src={`${assestsUtilityUrl}/back.svg`}
              style={{
                height: "2rem",
                width: "2rem",
              }}
              onClick={onNavigate}
            />
            <h6>{t("all_leave")}</h6>
          </div>
          <FormCalendarInput
            label={t("select_leave_academic_year")}
            placeholder={t("select_date")}
            name={"calendar"}
            value={state}
            onChange={onDateGrab}
            customFormInputContainer={{
              flexDirection: "row",
              gap: "1rem",
            }}
          />
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("n"))}
            parentState={getQuery.state}
            url={
              getQuery.state?.urlOffSet === "Institute"
                ? `/q/${params.username}/institute/member/lms?type=leave${tab.urlKey}`
                : `/q/${params.username}/member/staff/lms?type=leave${tab.urlKey}`
            }
          />
        ))}
      </HorizontalTabWrapper>
      <BorderBottom
        customStyleBorder={{
          marginBlock: "0",
        }}
      />
      <UserMemberContentWrapper>
        {!searchParams.get("n") ? (
          <LmsAllLeaveTeaching
            instituteId={instituteId}
            leaveTypeToggle={leaveTypeToggle}
          />
        ) : searchParams.get("n") === "nonteaching" ? (
          <Suspense fallback={<QvipleLoader />}>
            <LmsAllLeaveNonTeaching
              instituteId={instituteId}
              leaveTypeToggle={leaveTypeToggle}
            />
          </Suspense>
        ) : null}
      </UserMemberContentWrapper>
      {disabled && <QvipleLoader />}
    </UserMemberContainerWrapper>
  );
};

export default LmsAllLeave;

const tabList = [
  {
    label: "teaching_faculty",
    id: "teaching_faculty",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "non_teaching_faculty",
    id: "non_teaching_faculty",
    urlKey: "&n=nonteaching",
    groupIs: ["nonteaching"],
  },
];
